.Profile h2, h4, h1 {
    font-family: 'Bree Serif', serif;
}

.Profile h1 {
    margin-top: 0;
    text-transform: uppercase;
    font-size: 50px;
}

.Profile p {
    font-family: 'Raleway', sans-serif;
    margin-top: 0;
}

.title {
    font-size: 48px;
    font-weight: normal;
    line-height: 1;
}

p {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

ul {
    padding: 0px;
}

.social-icons li {
    display: inline;
}

.social-icons a {
    display: inline-block;
    margin: 10px;
    text-indent: -9999px;
    -webkit-transition: all .1s ease-in-out;
    -o-transition: all .1s ease-in-out;
    transition: all .1s ease-in-out;
}

.grow { -webkit-transition: all .2s ease-in-out; -o-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }
.social-icons a:hover { 
    -webkit-transform: scale(1.3); 
        -ms-transform: scale(1.3); 
            transform: scale(1.3); 
}

.twitter {
    background-image: url(./img/twitter.png);
    width: 24px;
    height: 24px;
}

.facebook {
    background-image: url(./img/facebook.png);
    width: 24px;
    height: 24px;
}

.googleplus {
    background-image: url(./img/googleplus.png);
    width: 24px;
    height: 24px;
}

.github {
    background-image: url(./img/github-circle.png);
    width: 24px;
    height: 24px;
}

.gmail {
    background-image: url(./img/gmail.png);
    width: 24px;
    height: 24px;
}

.linkedin {
    background-image: url(./img/linkedin-logo.png);
    width: 30px;
    height: 30px;
}

.dribbble {
    background-image: url(./img/dribbble.png);
    width: 21px;
    height: 19px;
}

.instagram {
    background-image: url(./img/instagram.png);
    width: 20px;
    height: 19px;
}

.flickr {
    background-image: url(./img/flickr.png);
    width: 20px;
    height: 9px;
}

.twitter .facebook .googleplus .github img {
    height: 100%;
    width: 100%;
}


